
html,
.container,
.imgbox {
  width: 100%;
}
body {
  margin: 0;
}
.imgbox {
  position: relative;
}
.imgbox img {
  width: 100%;
  margin-top: -0.015rem;
}
img {
  width: 100%;
  pointer-events: none;
}
.btn {
  width: 100%;
  height: 1.18rem;
  position: absolute;
}
.btn a {
  text-decoration: none;
  margin: 0 auto;
  display: block;
  width: 4.6rem;
  height: 1.18rem;
  background: #ff3a1e;
  border-radius: 0.58rem;
  text-align: center;
  color: #fff;
  line-height: 1.18rem;
  font-size: 0.38rem;
  font-family: '\82F9\65B9\4E2D\7C97\9ED1';
}